
  .choosen-dates-count {
    display: flex;
    justify-content: space-around;
    /* align-items: center; */
    color: white;
    background-color: rgb(185, 131, 131);
    border-radius: 50%;
    min-width: 18px;
    height: 18px;
    padding-bottom: 1px;
    margin-left: 3px;
    margin-right: 3px;
  }

  .choosen-dates-item {
    /* background-color: palevioletred; */
    display: flex;
    justify-content: space-between;
    /* margin-top: 2%; */
    height: 22px;
    margin-bottom: 2%;
  }

  .choosen-dates-wrap {
    border-radius: 12px;

    display: flex;
    flex-direction: row;
    justify-items: flex-start;
  }

  .choosen-dates-wrap:hover {
    padding-left: 1%;
    padding-right: 1%;
  }
  
  .choosen-dates-item:lang(Hebrew) {
    direction: rtl;
  }

  .choosen-dates {
    max-height: 18px;
    text-overflow: clip; 
    overflow: hidden;
  }

  @keyframes swing-in-top-fwd {
    0% {
      -webkit-transform: rotateX(-100deg);
              transform: rotateX(-100deg);
      -webkit-transform-origin: top;
              transform-origin: top;
      opacity: 0;
    }
    100% {
      -webkit-transform: rotateX(0deg);
              transform: rotateX(0deg);
      -webkit-transform-origin: top;
              transform-origin: top;
    }
  }
  
  .x-icon {
    margin-top: 3px;
    height: 14px;
    width: 14px;
    /* margin-right: 10%; */
    opacity: 0.4;
    margin-left: 5%;
    position: static;
    animation: swing-in-top-fwd 1s cubic-bezier(0.175, 0.885, 0.320, 1.275) both;
  }

  .x-icon:hover {
    opacity: 0.8;
  }
